<template>
  <div class="app-container">
    <div class="total-layout">
      <el-row :gutter="20">
        <el-col :span="6">
          <div class="total-frame">
            <svg-icon icon-class="order" class="total-icon"></svg-icon>
            <div>
              <div class="total-title">今日已完成订单总数</div>
              <div class="total-value">{{SalesDTOList.TodaySalesCount}}</div>
            </div>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="total-frame">
            <svg-icon icon-class="total-today" class="total-icon"></svg-icon>
            <div>
              <div class="total-title">今日完成销售总额</div>
              <div class="total-value">￥{{SalesDTOList.TodaySalesMoney}}</div>
            </div>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="total-frame">
            <svg-icon icon-class="total-yesterday" class="total-icon"></svg-icon>
            <div>
              <div class="total-title">昨日销售总额</div>
              <div class="total-value">￥{{SalesDTOList.YesterdaySalesMoney}}</div>
            </div>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="total-frame">
            <svg-icon icon-class="total-week" class="total-icon"></svg-icon>
            <div>
              <div class="total-title">近7天销售总额</div>
              <div class="total-value">￥{{SalesDTOList.SevendaysSalesMoney}}</div>
            </div>
          </div>
        </el-col>
      </el-row>
    </div>
    <div class="statistics-layout">
      <div class="layout-title">订单统计</div>
      <el-row>
        <el-col>
          <div style="padding: 10px;border-left:1px solid #DCDFE6">
            <div class="left">
              <div class="left" style="margin-right:10px;">
                <span style="color: #909399;font-size: 14px">订单总数</span>
                <span style="color: #606266;font-size: 24px;">{{ordersReceived.OrderTolCount}}</span>
              </div>
              <div class="left">
                <span style="color: #909399;font-size: 14px">销售总额</span>
                <span style="color: #606266;font-size: 24px">{{ordersReceived.OrderTolMoney}}</span>
              </div>
            </div>
            <el-date-picker
              style="float: right;z-index: 1"
              size="small"
              v-model="orderCountDate"
              type="daterange"
              align="right"
              unlink-panels
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              @change="handleDateChange"
              :picker-options="pickerOptions"
            ></el-date-picker>
            <div>
              <ve-line
                :data="chartData"
                :legend-visible="true"
                :loading="loading"
                :data-empty="dataEmpty"
                :settings="chartSettings"
              ></ve-line>
            </div>
          </div>
        </el-col>
      </el-row>
    </div>
    <div class="un-handle-layout">
      <div class="layout-title">待处理事务</div>
      <div class="un-handle-content">
        <el-row :gutter="20">
          <el-col :span="8">
            <div class="un-handle-item" @click="goOrder(2)">
              <span class="font-medium">已完成订单</span>
              <span style="float: right" class="color-danger">({{TransDTO.CompleteCount }})</span>
            </div>
          </el-col>
          <el-col :span="8">
            <div class="un-handle-item" @click="goOrder(1)">
              <span class="font-medium">待接单订单</span>
              <span style="float: right" class="color-danger">({{TransDTO.WaitingListCount }})</span>
            </div>
          </el-col>
          <el-col :span="8">
            <div class="un-handle-item" @click="goOrder(2)">
              <span class="font-medium">已接单订单</span>
              <span style="float: right" class="color-danger">({{TransDTO.HaveOrderCount }})</span>
            </div>
          </el-col>
        </el-row>
      </div>
    </div>
    <div class="overview-layout">
      <el-row>
        <el-col>
          <div class="out-border">
            <div class="layout-title">商品总览</div>
            <div style="padding: 40px">
              <el-row>
                <el-col
                  :span="8"
                  class="color-danger overview-item-value"
                 @click.native="goShop(0)"
                >{{StaticDTO.ShelvesCount}}</el-col>
                <el-col
                  :span="8"
                  class="color-danger overview-item-value"
                    @click.native="goShop(1)"
                >{{StaticDTO.TheShelvesCount}}</el-col>
                <el-col
                  :span="8"
                  class="color-danger overview-item-value"
                    @click.native="goShop()"
                >{{StaticDTO.TotalGoodsCount}}</el-col>
              </el-row>
              <el-row class="font-medium">
                <el-col :span="8" class="overview-item-title"   @click.native="goShop(0)">已下架</el-col>
                <el-col :span="8" class="overview-item-title"   @click.native="goShop(1)">已上架</el-col>
                <el-col :span="8" class="overview-item-title"   @click.native="goShop()">全部商品</el-col>
              </el-row>
            </div>
          </div>
        </el-col>
      </el-row>
    </div>
    <!-- <div class="overview-layout">
      <el-row>
        <el-col>
          <div class="out-border">
            <div class="layout-title">用户总览</div>
            <div style="padding: 40px">
              <el-row>
                <el-col :span="6" class="color-danger overview-item-value">100</el-col>
                <el-col :span="6" class="color-danger overview-item-value">200</el-col>
                <el-col :span="6" class="color-danger overview-item-value">1000</el-col>
                <el-col :span="6" class="color-danger overview-item-value">5000</el-col>
              </el-row>
              <el-row class="font-medium">
                <el-col :span="6" class="overview-item-title">今日新增</el-col>
                <el-col :span="6" class="overview-item-title">昨日新增</el-col>
                <el-col :span="6" class="overview-item-title">本月新增</el-col>
                <el-col :span="6" class="overview-item-title">会员总数</el-col>
              </el-row>
            </div>
          </div>
        </el-col>
      </el-row>
    </div>-->
  </div>
</template>

<script>
import { str2Date } from "@/utils/date";
// const DATA_FROM_BACKEND = {
//   columns: ["date", "orderCount", "orderAmount"],
//   rows: [
//     {
//       date: "2018-11-15",
//       orderCount: 40,
//       orderAmount: 4293
//     }
//   ]
// };
// import {
//   GetSalesDTO,
//   GetGoodsStaticDTO,
//   GetSalesChartDTO,
//   GetPendingTransDTO
// } from "@/api/home";
export default {
  name: "home",
  data() {
    return {
      pickerOptions: {
        shortcuts: [
          {
            text: "昨天",
            onClick(picker) {
              //设置最近一周的时间v-line,
              const end = new Date();
              let start = new Date();
              // start.setFullYear(2018);
              // start.setMonth(10);
              // start.setDate(1);
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 1);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近一周",
            onClick(picker) {
              //设置最近一周的时间v-line,
              const end = new Date();
              let start = new Date();
              // start.setFullYear(2018);
              // start.setMonth(10);
              // start.setDate(1);
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近一月",
            onClick(picker) {
              //设置最近一月的时间v-line,
              const end = new Date();
              let start = new Date();
              // start.setFullYear(2018);
              // start.setMonth(10);
              // start.setDate(1);
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      orderCountDate: "",
      chartSettings: {
        // xAxisType: "time",
        area: true,
        axisSite: {
          right: ["OrderMoney"]
        },
        yAxisName: ['数量', '金额'],
        labelMap: {
          OrderCount: "订单数量",
          OrderMoney: "订单金额",
          OrderTime:"日期"
        }
      },
      chartData: {
        columns: ["OrderTime", "OrderCount", "OrderMoney"],
        rows: [],
      },
      ordersReceived:{OrderTolCount:'',OrderTolMoney:''},
      loading: false,
      dataEmpty: false,
      SalesDTOList: {},
      StaticDTO: {},
      TransDTO: {}
    };
  },
  created() {
    this.initOrderCountDate();
    // this.getData();
    this.SalesDTO();
    this.GoodsStaticDTO();
    this.PendingTransDTO();
  },
  // watch: {
  //   orderCountDate(newV, oldV) {
  //     // console.log(newV);
  //   }
  // },
  methods: {
    goOrder(val){
      this.$router.push({path:'oms/orderDelivery',query:{status:val}});
    },
      goShop(val){
      let url={path:'pms/product'}
      if(val==0||val){
        url.query={IsShelves:val}
      }
      this.$router.push(url);
    },
    SalesDTO() {
      //查询业绩统计
      this.$request.GetSalesDTO().then(res => {
        this.SalesDTOList = res.content;
      });
    },
    GoodsStaticDTO() {
      //查询商品总览
      this.$request.GetGoodsStaticDTO().then(res => {
        this.StaticDTO = res.content;
        //ShelvesCount":下架,"TheShelvesCount":上架,"TotalGoodsCount":总计
      });
    },
    SalesChartDTO() {
      //查询选择时间段内销售订单数据
       this.$request.GetSalesChartDTO(this.orderCountDate[0], this.orderCountDate[1]).then(
        res => {
          res=res.content;
          this.$set(this.ordersReceived,'OrderTolCount',res.OrderTolCount);
           this.$set(this.ordersReceived,'OrderTolMoney',res.OrderTolMoney);
           let SalesChartList=res.SalesChartList;
           if(!SalesChartList){
             this.chartData.rows=SalesChartList;
             return;
           }

           for(let i=0;i<SalesChartList.length;i++){
              let OrderTime=SalesChartList[i].OrderTime;
              if(OrderTime.indexOf('T')>-1){
                SalesChartList[i].OrderTime=OrderTime.split('T')[0];
              }
           }
            this.chartData.rows=SalesChartList;
        }
      );
    },
    PendingTransDTO() {
       this.$request.GetPendingTransDTO().then(res => {
        this.TransDTO = res.content;
      });
    },
    handleDateChange(value) {
      //选择时间
      this.getData();
    },
    initOrderCountDate() {
      let start = new Date();
      // start.setFullYear(2018);
      // start.setMonth(10);
      // start.setDate(1);
      const end = new Date();
      start.setTime(start.getTime() - 1000 * 60 * 60 * 24 * 7);
      this.orderCountDate = [start, end];
      this.SalesChartDTO(this.orderCountDate[0], this.orderCountDate[1]);
    },
    getData() {
      this.loading = true;
      setTimeout(() => {
        this.chartData.rows = [];
        // this.chartData = {
        //   columns: ["date", "orderCount", "orderAmount"],
        //   rows: []
        // }; //m每次选择都重置
        // for (let i = 0; i < DATA_FROM_BACKEND.rows.length; i++) {
        //   let item = DATA_FROM_BACKEND.rows[i];
        //   let currDate = str2Date(item.date);
        //   let start = this.orderCountDate[0]; //echars图选择的上下时间
        //   let end = this.orderCountDate[1];
        //   if (
        //     currDate.getTime() >= start.getTime() &&
        //     currDate.getTime() <= end.getTime()
        //   ) {
        //     this.chartData.rows.push(item);
        //     //在选择时间范围内push进去
        //   }
        // }
        this.dataEmpty = false;
        this.loading = false;
        this.SalesChartDTO(this.orderCountDate[0], this.orderCountDate[1]);
      }, 1000);
    }
  }
};
</script>

<style scoped>
/* .app-container {
    margin-top: 40px;
    margin-left: 120px;
    margin-right: 120px;
  } */

/* .address-layout {} */
.left {
  float: left;
}
.total-layout {
  margin-top: 20px;
}

.total-frame {
  border: 1px solid #dcdfe6;
  padding: 20px;
  height: 100px;
  display: flex;
}
.total-frame>div{
  padding:10px;
  padding-left:10px;
}
.total-icon {
  color: #409eff;
  width: 60px;
  height: 60px;
}

.total-title {
  /* position: relative;
  left: 70px;
  top: -50px; */
  font-size: 16px;
  color: #909399;
  padding-bottom:5px;
}

.total-value {
  /* position: relative;
  left: 70px;
  top: -40px; */
  font-size: 18px;
  color: #606266;
}

.un-handle-layout {
  margin-top: 20px;
  border: 1px solid #dcdfe6;
}

.layout-title {
  color: #606266;
  padding: 15px 20px;
  background: #f2f6fc;
  font-weight: bold;
}

.un-handle-content {
  padding: 20px 40px;
}

.un-handle-item {
  border-bottom: 1px solid #ebeef5;
  padding: 10px;
  cursor: pointer;
}

.overview-layout {
  margin-top: 20px;
}

.overview-item-value {
  font-size: 24px;
  text-align: center;
  cursor: pointer;
}

.overview-item-title {
  margin-top: 10px;
  text-align: center;
  cursor: pointer;
}

.out-border {
  border: 1px solid #dcdfe6;
}

.statistics-layout {
  margin-top: 20px;
  border: 1px solid #dcdfe6;
}
</style>



